import React from 'react'
import { Container } from 'react-bootstrap'
import LayoutCustom from "../components/LayoutCustom";
import Footer from "../components/Footer";

const UnderConstruction = () => {
	return (
		<LayoutCustom>
			<div className="main-content-padding">
				<div className="app-section-underconstruction">
					<Container>
						<div className="app-underconstruction-detail">
							<div className="app-underconstruction-detail-content">
								<h2>This page (Our website) is under Maintenace Mode.</h2>
								<h6>Thank you for visting our website.</h6>
								<h6>However, we are in the process of making it better.</h6>
								<h6>Please come back another time.</h6>
							</div>
						</div>
					</Container>
					<div className="footer-custom">
						<Footer />
					</div>
				</div>
			</div>
		</LayoutCustom>
	)
}

export default UnderConstruction
